.site-nav__icon {
  height: 3px;
  width: 30px;
  cursor: pointer;
  background-color: #3c322d;
  transition: background-color .4s ease-in-out .1s;
  display: inline-block;
  position: relative;
}

.site-nav__icon:before {
  content: "";
  height: 3px;
  width: 30px;
  cursor: pointer;
  background-color: #3c322d;
  transition: transform .4s ease-in-out;
  display: inline-block;
  position: absolute;
  bottom: 8px;
  left: 0;
}

.site-nav__icon:after {
  content: "";
  height: 3px;
  width: 30px;
  cursor: pointer;
  background-color: #3c322d;
  transition: transform .4s ease-in-out;
  display: inline-block;
  position: absolute;
  top: 8px;
  left: 0;
}

.open-mode {
  background-color: rgba(0, 0, 0, 0);
}

.open-mode:before {
  transform: translateY(260%)rotate(45deg);
}

.open-mode:after {
  transform: translateY(-250%)rotate(-45deg);
}

