  box-shadow: 0 7px 27px -2px rgba(253, 88, 140, .35);
}

.preloader {
  background: #e0e0e0;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}

.preloader__image {
  width: 150px;
  height: 150px;
  text-align: center;
  margin-left: -35px;
  animation: preloader-rotate 5s linear infinite;
  position: relative;
  top: 40%;
  left: 50%;
}

@keyframes preloader-rotate {
  100% {
    transform: rotate(360deg);
  }
}

.loaded_hiding .preloader {
  opacity: 0;
  transition: opacity 10s;
}

.loaded .preloader {
  display: none;
}

.preloader-text {
  width: 30px;
  height: 30px;
  text-align: center;
  margin-top: -35px;
  margin-left: -35px;
  position: relative;
  top: 40%;
  left: 45%;
}

.modal-window {
  z-index: 20;
